.Music-songAnimate {
  height: 12px;
  padding-right: 6px;
  position: absolute;
  right: 0;
  top: 11px; }
  .Music-songAnimate > i {
    position: relative;
    display: inline-block;
    width: 3px;
    height: 100%;
    margin-right: 2px; }
    .Music-songAnimate > i:after {
      position: absolute;
      bottom: 0;
      width: 100%;
      height: 100%;
      content: '';
      background-color: #e03a3a; }
    .Music-songAnimate > i:nth-of-type(1):after {
      animation: waves .6s linear 0s infinite alternate; }
    .Music-songAnimate > i:nth-of-type(2):after {
      animation: waves .9s linear 0s infinite alternate; }
    .Music-songAnimate > i:nth-of-type(3):after {
      animation: waves 1.2s linear 0s infinite alternate; }

